import React, { useState } from 'react';
import styled from 'styled-components';
import * as _ from 'lodash-es';
import { BiCurrentLocation, BiPlus, BiMinus, BiMapAlt } from 'react-icons/bi';
import { useDispatch } from 'react-redux';
import {
  zoomIn,
  zoomOut,
  setCurrentPosition,
} from '../../features/mapControlSlice';

const MapControlWrapper = styled.nav`
  position: absolute;
  z-index: 20;
  top: 6rem;
  right: 1.25rem;
`;

const MapControlButtonWrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 2rem;
`;

const MapControlButton = styled.div`
  width: 2.3rem;
  height: 2.3rem;
  line-height: 2.3rem;
  text-align: center;
  background-color: white;
  border: 1px solid #b9bbbe;
  border-radius: 0.375rem;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: var(--shadow-md);
  & svg {
    width: 1.5rem;
    height: 2rem;
    display: inline-block;
  }
  & + & {
    box-shadow: var(--shadow-none);
    border-bottom: 1px solid #b9bbeb;
  }
`;

const ZoomButtonWrapper = styled.div`
  width: 2.5rem;
  height: 5rem;
`;

const MapControlButtonSubWrapper = styled.div`
  visibility: ${props => props.visibility || 'hidden'};
  position: absolute;
  right: 3rem;
  top: 9.75rem;
  display: flex;
  flex-direction: row;
  justify-content: end;
  width: 9rem;
  gap: 0.75rem;
  line-height: 2.5rem;
  text-align: center;
  height: 2.5rem;
  box-shadow: var(--shadow-md);
`;

// const MapControl = ({ mapControls }) => {
const MapControl = () => {
  const dispatch = useDispatch();
  const [visibility, setVisibility] = useState('hidden');
  const handleVisibilityToggle = e => {
    _.isEqual(visibility, 'hidden')
      ? setVisibility('visible')
      : setVisibility('hidden');
  };

  const mapControls = [
    {
      id: 'mapMyLoc',
      title: '  ',
      icon: <BiCurrentLocation />,
      onClickFunction: null,
    },
    {
      id: 'mapZoomIN',
      title: '',
      icon: <BiPlus />,
      onClickFunction: null,
    },
    {
      id: 'mapZoomOut',
      title: '',
      icon: <BiMinus />,
      onClickFunction: null,
    },
    {
      id: 'mapLayers',
      title: '',
      icon: <BiMapAlt />,
      onClickFunction: handleVisibilityToggle,
      subMenu: [
        {
          id: 'mapBaseHybrid',
          title: '',
          onClickFunction: null,
          name: '',
        },
        {
          id: 'mapBaseUseDistrict',
          title: '',
          onClickFunction: null,
          name: '',
        },
        {
          id: 'mapBaseTerrain',
          title: '',
          onClickFunction: null,
          name: '',
        },
      ],
    },
  ];
  return (
    <MapControlWrapper>
      <MapControlButtonWrapper>
        <MapControlButton
          key="mapMyLoc"
          onClick={() => dispatch(setCurrentPosition())}
          title="  "
        >
          <BiCurrentLocation />
        </MapControlButton>
        <ZoomButtonWrapper>
          <MapControlButton
            key="mapZoomIn"
            onClick={() => dispatch(zoomIn())}
            title=""
          >
            <BiPlus />
          </MapControlButton>
          <MapControlButton
            key="mapZoomOut"
            onClick={() => dispatch(zoomOut())}
            title=""
          >
            <BiMinus />
          </MapControlButton>
        </ZoomButtonWrapper>
      </MapControlButtonWrapper>
    </MapControlWrapper>
  );
};

export default MapControl;
